<template>
  <section class="section" id="about">
    <div class="inner">
      <div class="section-title-block">
        <h2 class="section-title">ABOUT</h2>
        <p class="section-desc">
          こんにちは。ひょうどうたつやと申します。
        </p>
      </div>
      <div class="two-column-wrapper">
        <div class="two-column-image">
          <img src="https://qiita-user-profile-images.imgix.net/https%3A%2F%2Fqiita-image-store.s3.amazonaws.com%2F0%2F42110%2Fprofile-images%2F1473689041?ixlib=rb-1.2.2&auto=compress%2Cformat&lossless=0&w=300&s=7ba3ca77a17846d6a0ffc6ed381fc220" alt="">
        </div>
        <div class="two-column-desc">
          <p class="text" v-html="body"></p>
          <p class="social">
            <a :href="val.url" v-for="(val, name) in social" :key="name" target="_blank">
              <img v-if="val.icon" :src="val.icon">
              <span v-else>{{name}}</span>
            </a>
          </p>
          <div class="article">
            <div>寄稿</div>
            <a :href="article.url" v-for="article in articles" :key="article.title" target="_blank">
              {{article.title}}
            </a>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'AboutView',
  props: {
    tasks: Array
  },
  data () {
    return {
      body: '1982年生まれ。鹿児島大学理学部を卒業後、2005年にSI系会社へ入社。<br>業務系基幹システムの構築や、EC、物流、ファッションサイト構築など幅広い分野の設計、製造を経験。<br>株式会社エンファクトリーでは社内事業の全システムの担当を行い、受託事業の立ち上げ、要件定義から開発、納品まで一貫したシステム開発を行う。<br>現在は、複数社のIT部門統括と受託開発をしつつ自社サービスの開発を進める。',
      social: {
        'facebook': {'url': 'https://www.facebook.com/tentatsu', 'icon': ''},
        'twitter': {'url': 'https://twitter.com/tentatsu', 'icon': ''},
        'github': {'url': 'https://github.com/tentatsu', 'icon': ''},
        'qiita': {'url': 'https://qiita.com/tentatsu', 'icon': ''},
        'Teamlancer': {'url': 'https://teamlancer.jp/users/48', 'icon': ''}
      },
      articles: [
        {'title': 'Pythonエンジニアの将来性について', 'url': 'https://career.levtech.jp/guide/knowhow/article/474/'},
        {'title': 'Pythonエンジニアの年収が高い理由', 'url': 'https://career.levtech.jp/guide/knowhow/article/473/'},
        {'title': 'Pythonエンジニアの転職は本当に有利なのか', 'url': 'https://career.levtech.jp/guide/knowhow/article/479/'}
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .social {
    a {
      margin: 5px;
    }
  }
  .article {
    a {
      display: block;
    }
  }
</style>



