<template>
  <section class="section" id="qiita">
    <div class="inner">
      <div class="section-title-block">
        <h2 class="section-title">Qiita</h2>
        <div class="section-desc">
          <p class="text">
            Qiitaの投稿一覧です。</p>
        </div>
      </div>
      <div class="card-wrapper my-gallery">
        <figure v-for="item in ListItems" :key="item.title" class="card">
          <figcaption class="card-caption">
            <div class="left">
            <p class="card-created">{{item.created_at | moment}}</p>
            <p class="card-title"><a class="url-link" :href="item.url" target="_blank">{{item.title}}</a></p>
            <p class="card-tag"><span v-for="tag in item.tags" :key="tag.name">{{tag.name}}</span></p>
            </div>
            <div class="right">
            <p class="card-count">{{item.likes_count}}</p>
            </div>
          </figcaption>
        </figure>
          <button
            v-if="(ListItems.length - count) >= 0"
            class="st-motto"
            type="button"
            @click="isMore"
          >
            もっとみる
          </button>
      </div>
    </div>
  </section>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
export default {
  name: 'QiitaView',
  props: {
    tasks: Array
  },
  data () {
    return {
      items: [
      ],
      count: 4
    }
  },
  created () {
    const request = axios.create({
      baseURL: 'https://qiita.com/api/v2/'
    })
    request.get(`users/tentatsu/items?page=1&per_page=100`).then(res => {
      this.items = res.data
    })
  },
  filters: {
    moment: function (date) {
      return moment(date).format('YYYY/MM/DD')
    }
  },
  computed: {
    ListItems () {
      const list = this.items
      return list.slice(0, this.count)
    }
  },
  methods: {
    isMore () {
      this.count += 10
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
$pc: 1024px; // PC
$tab: 680px; // タブレット
$sp: 480px;  // スマホ
  .card {
    width: 48%;
    text-align: left;
    border-top: 1px solid #CCC;
    margin-bottom: 20px;
    padding-top: 20px;
    @media (max-width: ($tab)) {
      width: 100%;
    }
    p {
      margin-bottom: 0px;
    }

    &-title {
      font-size: 12px;
    }
    &-info {
      font-size: 11px;
    }
    &-wrapper {
      padding: 20px 5px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      -webkit-box-pack: justify;
      -ms-flex-pack: justify;
      justify-content: space-between;
      @media (max-width: ($tab)) {
        padding: 0;
      }
    }
    &-tag {
      margin: 10px;
      span {
        font-size: 10px;
        margin-right: 10px;
        padding: 7px;
        background-color: #CCC;
        border-radius: 30%;
      }
    }
    .left {
      max-width: 80%;
    }
  }
  .st-motto {
    display: block;
    width: 100%;
    text-align: center;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #ccc;
    margin: 20px 5%;
    padding: 10px;
  }
</style>



