<template>
  <section class="section" id="skill">
    <div class="inner">
      <div class="section-title-block">
        <h2 class="section-title">SKILL</h2>
        <p class="section-desc">
          プロジェクトマネージメントとDB設計がすき。<br>技術内容がマネージに寄ってきてるなぁ・・・
        </p>
      </div>
      <div class="card-wrapper">
        <div class="card" v-for="item in items" :key="item.title">
          <!-- div class="skill-icon">
            <img src="img/skill/icon_skill.png" alt="">
          </div -->
          <div class="skill-desc">
            <h3 class="skill-desc-title">{{item.title}}</h3>
            <div class="rating">
              <p :class="item.rate"></p>
            </div>
            <p class="text" v-html="item.body"></p>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'SkillView',
  props: {
    tasks: Array
  },
  data () {
    return {
      items: [
        {
          title: 'DB設計、全体設計',
          rate: 'rate rate5',
          body: '最近こればっかりやってるなぁ。設計得意です。設計ばっかりやってます。'
        },
        {
          title: 'swagger(OpenAPI)',
          rate: 'rate rate5',
          body: 'VueなどSPAなサイト構築が増えてきて、API設計をするならこれがないと始まらない。'
        },
        {
          title: 'Python',
          rate: 'rate rate4',
          body: 'イチから書くのはちょっと骨が折れる。Django便利。スバラシイ。'
        },
        {
          title: 'MySQL/PostgreSQL',
          rate: 'rate rate5',
          body: 'データベースならあまり好き嫌いなく出来ます。RDSとかCloudSQLでも大丈夫。安心。'
        },
        {
          title: 'PHP',
          rate: 'rate rate5',
          body: 'とりあえず何か作ってって言われたらPHPかな。Larabel、Cake3系、WP、ECCubeなど一通り触れます。'
        },
        {
          title: 'Perl',
          rate: 'rate rate4',
          body: '昔はPerlでやってたなぁ。。今でもワンライナーとかはPerl使ったりします。とりあえず入ってるし文字列に柔軟！'
        },
        {
          title: 'Kubernetes',
          rate: 'rate rate4',
          body: '最近一番アツい技術。Dockerを知ってから一気に広がったなー。本格運用をやりたい。'
        },
        {
          title: 'gitbook',
          rate: 'rate rate5',
          body: '設計書はこれで書いてます。gitで管理できるのが◎'
        },
        {
          title: 'HTML/CSS',
          rate: 'rate rate4',
          body: '嗜む程度に。CSSの設計は苦手。やれと言われればやります。'
        },
        {
          title: 'VueJS',
          rate: 'rate rate5',
          body: 'このサイトもVueで作ってみた。たのしーーー！'
        }

      ]
    }
  }
}
</script>

